<!--
 * @Description: 修改当亲登录的密码
 * @LastEditors: Ji Yinghao
 * @Date: 2019-05-07 17:51:44
 * @LastEditTime: 2019-09-27 16:02:01
 -->

<template>
  <div>
    <v-jumbotron>
      <v-container fill-height>
        <v-layout align-center>
          <v-flex>
            <h3 class="display-3">修改登录密码</h3>
            <!-- <span class="subheading"></span> -->
            <v-divider class="my-3"></v-divider>
            <div class="title mb-3">请妥善保管密码 &nbsp; (首次登陆用户建议修改一个新的密码)!</div>
            <!-- <v-btn class="mx-0" color="primary" large>See more</v-btn> -->
            <form>
              <v-text-field
                v-model="userName"
                :rules="[nameRules.required, nameRules.min]"
                type="text"
                maxlength="16"
                name="input-10-1"
                label="输入旧密码"
                hint
                counter
              ></v-text-field>
              <v-text-field
                v-model="password"
                :append-icon="show2 ? 'visibility' : 'visibility_off'"
                :rules="[passwordRules.required, passwordRules.min]"
                :type="show2 ? 'text' : 'password'"
                name="input-10-2"
                maxlength="26"
                label="输入新密码"
                hint
                counter
                @click:append="show2 = !show2"
              ></v-text-field>
              <v-text-field
                v-model="password_"
                :append-icon="show3 ? 'visibility' : 'visibility_off'"
                :rules="[passwordRules_.required, passwordRules_.min]"
                :type="show3 ? 'text' : 'password'"
                name="input-10-2"
                maxlength="26"
                label="再次输入新密码"
                hint
                counter
                @click:append="show3 = !show3"
              ></v-text-field>
              <!-- <v-switch
                  v-model="checkbox_"
                  color="orange darken-3"
                  hide-details
                  :label="`是否记住密码: ${checkbox_?'是':'否'}`"
              ></v-switch>-->
              <br>
               <i> （ *修改密码需要重新登录 ） </i>
               <br>
               <br>
              <v-btn color="info" :loading="loader" :disabled="loader" @click="submit_">提交</v-btn>
              <!-- <v-btn @click="cookies">cookies</v-btn>
              <v-btn @click="del">del</v-btn>-->
            </form>
          </v-flex>
        </v-layout>
      </v-container>
    </v-jumbotron>
  </div>
</template>

<script>
import Api from "../../../server/api.js";
import Cookies from "../../../server/cookies.js";
export default {
  data() {
    return {

      loader: false,
      userName: "",
      password: "",
      password_: "",
      //   checkbox_: false,
      show1: false,
      show2: false,
      show3: false,
      nameRules: {
        required: value => !!value || "请输入旧密码",
        min: v => v.length >= 5 || "至少6-26位字符",
        emailMatch: () => "The email and password you entered don't match"
      },
      passwordRules: {
        required: value => !!value || "用户密码为6-26位",
        min: v => v.length >= 6 || "至少6位字符",
        emailMatch: () => "The email and password you entered don't match"
      },
      passwordRules_: {
        required: value => !!value || "用户密码为6-26位",
        min: v => v.length >= 6 || "至少6位字符",
        emailMatch: () => "The email and password you entered don't match"
      }
    };
  },
  methods: {
    // 提交新密码
    submit_() {
      if (this.userName.length < 5) {
        this.$Dialog.getProductCheckResult_({text: `输入管理员旧密码不小于6位！`,timeout: 3000, snackbar: true });
        return false;
      }
      if (this.password.length < 6) {
        this.$Dialog.getProductCheckResult_({text:  `输入管理员新密码不小于6位！`,timeout: 3000, snackbar: true });
        return false;
      }
      if ( this.password_ !== this.password ) {
        this.$Dialog.getProductCheckResult_({text: `两次密码输入不一致！`,timeout: 3000, snackbar: true });
        return false;
      }
      this.$axios({
        method: "post",
        url: Api.URL.manages_modify_password,
        data: {
          old_password: this.userName,
          password: this.password,
          repassword: this.password_
        }
      })
        .then(response => {
          //请求成功返回的数据
          if (response.data.result === 0) {
            this.menu = false;
            if (Cookies.getCookie("user")) {
              Cookies.delCookie("user");
            }
            this.$Dialog.getProductCheckResult_({text: "密码已修改，请重新登录",timeout: 3000, snackbar: true });
            setTimeout(() => {
              this.$router.push({
                path: `/`
              });
            }, 1000);
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "😡" + error,timeout: 3000, snackbar: true });
        });
    }
  }
};
</script>

<style lang="less" scoped>
form {
  max-width: 600px;
  margin: 0 auto;
}
.v-jumbotron {
  height: 500px !important;
}
</style>
